.itcss {
  max-width: 100%;
  padding-left: 10px;
  padding-right: 10px;
  position: relative;
}

.itcss__wrapper {
  background-color: #eee;
  position: relative;
  overflow: hidden;
}

.itcss__items {
  transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  will-change: transform;
  transition: transform .5s;
  display: flex;
}

.itcss__item {
  max-width: 100%;
  transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  will-change: transform;
  flex: 0 0 100%;
  position: relative;
}

